//
// Color system
//

$white:  #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #868e96;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black:  #000;

$grays: (
  100: $gray-100,
  200: $gray-200,
  300: $gray-300,
  400: $gray-400,
  500: $gray-500,
  600: $gray-600,
  700: $gray-700,
  800: $gray-800,
  900: $gray-900
);

$blue:    #007bff;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #e83e8c;
$red:     #dc3545;
$orange:  #fd7e14;
$yellow:  #ffc107;
$green:   #28a745;
$teal:    #20c997;
$cyan:    #17a2b8;

$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  white: $white,
  gray: $gray-600,
  gray-dark: $gray-800
);

$primary:   #fcd11b;
$secondary: #f1f1f2;
$success:   #339900;
$info:      #2277cc;
$warning:   #ffbb00;
$danger:    #cc3300;
$light:     $gray-100;
$dark:      $gray-800;
$darker:    $gray-900;

$theme-colors: (
  primary:   $primary,
  secondary: $secondary,
  success:   $success,
  info:      $info,
  warning:   $warning,
  danger:    $danger,
  light:     $gray-100,
  dark:      $gray-800,
  darker:    $gray-900
);
// Set a specific jump point for requesting color jumps
$theme-color-interval:      8% !default;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold:  150 !default;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark:             $gray-900 !default;
$yiq-text-light:            $white !default;


$font-family-base: 'GT-Walsheim-Pro';

$base-border-width: 2px;
$base-focus-scale: 1.05;
$base-hover-scale: 1.2;

$button-o-border-width: $base-border-width; //.1em;
$button-o-font-size: 1rem;
$button-o-color: black; //theme-color("primary");
$button-o-border-color: theme-color("primary");
$button-o-bg-color: transparent;
$button-o-hover-color: black;
$button-o-hover-border-color: theme-color("primary");
$button-o-hover-bg-color: theme-color("primary");

$button-border-width: $base-border-width; //.1em;
$button-font-size: 2rem;
$button-color: black;
$button-border-color: theme-color("primary");
$button-bg-color: theme-color("primary");
$button-hover-color: theme-color("primary");
$button-hover-border-color: theme-color("secondary");
$button-hover-bg-color: theme-color("secondary");

$button-bg-image: (
  url(/assets/img/coin.png),
  url(/assets/img/face1.png),
  url(/assets/img/face2.png),
  url(/assets/img/face3.png),
  url(/assets/img/face4.png),
  url(/assets/img/face5.png),
  url(/assets/img/face6.png),
  url(/assets/img/face7.png)
);
$button-hover-bg-image: (
  url(/assets/img/coin.png),
  url(/assets/img/face1.png),
  url(/assets/img/face2.png),
  url(/assets/img/face3.png),
  url(/assets/img/face4.png),
  url(/assets/img/face5.png),
  url(/assets/img/face6.png),
  url(/assets/img/face7.png)
 );
