$scramble-font-family: "scramble" !default;
$scramble-font-path: "fonts" !default;

$scramble-logo: "\e900";
$scramble-dot: "\e901";
$scramble-plus: "\e902";
$scramble-cross: "\e903";
$scramble-smile: "\e904";
$scramble-shield: "\e905";
$scramble-notebook: "\e906";
$scramble-graph: "\e907";
$scramble-feather: "\e908";
$scramble-wallet: "\e909";
$scramble-money: "\e90a";
$scramble-coins: "\e90b";
$scramble-arrow-left: "\e90c";
$scramble-arrow-up: "\e90d";
$scramble-arrow-right: "\e90e";
$scramble-arrow-down: "\e90f";
$scramble-facebook: "\e910";
$scramble-linkedin: "\e911";




@font-face {
  font-family: '#{$scramble-font-family}';
  src: url('#{$scramble-font-path}/#{$scramble-font-family}.eot?v2');
  src: local('☺'),
    // url('#{$scramble-font-path}/#{$scramble-font-family}.eot?v1#iefix') format('embedded-opentype'),
    url('#{$scramble-font-path}/#{$scramble-font-family}.woff2?v2') format('woff2'),
    url('#{$scramble-font-path}/#{$scramble-font-family}.woff?v2') format('woff'),
    url('#{$scramble-font-path}/#{$scramble-font-family}.ttf?v2') format('truetype'),
    url("#{$scramble-font-path}/#{$scramble-font-family}.otf?v2") format("opentype"),
    url('#{$scramble-font-path}/#{$scramble-font-family}.svg?v2##{$scramble-font-family}') format('svg');

  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.scramble {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$scramble-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  // line-height: 0;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

