input {
  display: block;
  width: 100%;
  // margin: .5rem;
  padding: .5rem 0;
  border: none;
  border-radius: 0;
  border-bottom: 2px gray solid;
  background: transparent;
  &:focus, &:active, &:hover {
    outline: none;
    border: none;
    border-bottom: 2px theme-color("primary") solid;
  }
}
