
footer {
  padding: 3rem 2rem 3rem 4rem;
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  color: white;
  a {
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
    color: white;
  }
  .copyright {
    padding: 0 1rem;
    display:flex;
    // margin-left: auto;
    // margin-right:3rem; 
    .logo {
      width: 8rem;
      margin-right: 3rem;
    }
  }
  ul.social {
    padding: 0 1rem;
    // margin-left:3rem;
    li {
      display: inline-block;
      list-style: none;
      text-transform: uppercase;
      // margin-bottom: 1rem;
      margin: .5rem;
      font-size: 2em; 
    }
  }
  .address {
    padding: 0 1rem;
    // margin-left: auto;
    // margin-right:3rem; 
  }
  .muted {
    color: #4d4d4d;
  }
  .credits {
    display: none;
  }
}

