@import "header";
@import "main";
@import "footer";


#bg-animations {
  position: fixed;
  z-index: -10000;
  display: block;
  background: $white;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  transition: all .25s ease-in-out;
  canvas {
    display: block;
    opacity: 0;
    transform: scale(0);
    transition: all .25s ease-in-out;
  }
  .dot {
    // display: none;
    transform: scale(0);
    opacity: 0;
    transition: all .25s ease-in-out;
  }
  &.top {
    canvas {
      opacity: 1;
      transform: scale(1);
    }
  }
  &.header {
    canvas {
      opacity: 1;
      transform: scale(1);
    }
    .dot {
      display: none;
      margin: 17vmin calc(50% - 32vmin);
      padding: 33vmin;
      transform: scale(1);
      opacity: 1;
    }
  }
  &.about {
    canvas {
      opacity: 1;
      transform: scale(1);
    }
    .dot {
      display: none;
      margin: 17vmin calc(50% - 32vmin);
      padding: 33vmin;
      transform: scale(1);
      opacity: 1;
      // filter: blur(1rem);

    }
  }
}
div.dot {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  // padding: 0%;
  padding: 50%;
  // margin: 1rem;
  border-radius: 100%;
  background-color: theme-color("primary");
  border: $base-border-width solid theme-color("primary");
  transition: all .25s ease-in-out;
  transform: scale(0);
}

span.tooltip {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  opacity: 0;
  font-size: 1rem;
  text-transform: none;
  line-height: 1;
  padding: 1.5rem;
  background-color: white;
  border-radius: 2rem;
  border: 1px solid black;
  white-space: nowrap;
  transition:  all 0.25s ease-in-out;
  &::after, &::before {
    position: absolute;
    display: block;
    content: "";
    border-color: transparent;
    border-style: solid;
  }
  &::before {
    bottom: calc(-1.5rem - #{$base-border-width});
    border-width: calc(1.5rem + #{$base-border-width}) calc(1rem + #{$base-border-width}) 0 0;
    border-top-color: black;
  }
  &::after {
    bottom: calc(-1.5rem + #{$base-border-width});
    left: calc(1.5rem + #{$base-border-width});
    border-width: 1.5rem 1rem 0 0;
    border-top-color: #fff;
  }
}
ul.dot {
  >li {
    position: relative;
    margin-bottom: 2.5rem;
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      padding: 3.5rem;
      margin: 1.5rem 0 0 2.2rem;
      // margin: 1rem;
      border-radius: 100%;
      border: $base-border-width theme-color("primary") solid;
      background-color: theme-color("primary");
      z-index: -1;
      transition: all .25s ease-in-out .5s;
      transform: scale(0);
    }
    &:hover::before {
      transform: scale($base-hover-scale);
    }
  }
}
ul.sm-dot {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1rem;
  width: 0;
  // overflow: hidden;
  // font-size: 0;
  // font-size: 1em;
  opacity: 0;
  // line-height: 0;
  transition: width 0.25s ease-in-out .2s, opacity 0.25s ease-in-out;
  >li {
    // font-size: 1em;
    // font-size: 0;
    width: 0;
    position: relative;
    display: flex !important;
    margin-bottom: 2.5rem;
    padding-right: 2rem;
    &::before {
      content: $scramble-dot;
      font-family: "scramble" !important;
      font-size: 2em;
      line-height: .5;
      color: theme-color("primary");
      display: block;
      min-width: 1.5rem;
      margin-left: -1.5rem;
      // margin-right: 1rem;
    }
  }
  &.sm-dot.visible {
    // font-size: 1rem;
    opacity: 1;
    width: 100%;
    // line-height: 1.47;
    transition: width .25s ease-in-out, opacity 0.25s ease-in-out .2s;
    >li {
      // font-size: 1rem;
      // width: 100%;
    }
  }
}


ol.dot {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1rem;
  margin-right: -1rem;
  counter-reset: li;
  list-style: none;
  li {
    position: relative;
    display: block;
    counter-increment: li;
    padding: 3rem 6rem 4rem 4rem !important;
    // max-width: none;
    opacity: 0;
    transform: scale(0);
    transition: all 0.25s ease-in-out .5s;
    font-size: 1rem;
    &::before {
      content: counters(li,".") "";
      font-weight: 600;
      display: block;
      font-size: 2.5rem;
      opacity: 1;
      margin-bottom: .5rem;
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      padding: calc(50% - 1rem);
      // margin: 1rem;
      border-radius: 100%;
      border: $base-border-width theme-color("primary") solid;
      background-image: radial-gradient(theme-color("primary") 50%, transparent 50%, transparent 100%);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 0 0;
      transition: background-size .25s ease-in-out;
      z-index: -1
    }
    &:hover::after {
      // background-color: theme-color("primary");
      background-size: 200% 200%;
    }
  }
}

ul.radio {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  > li {
    display: flex;
    flex: 0 0 50%;
    font-weight: 600;
    margin-bottom: 3rem;
    text-transform: uppercase;
    cursor: pointer;
    &::before {
      content: "";
      flex: 0 0 2rem;
      display: block;
      width: 3rem;
      height: 2rem;
      margin-right: 3rem;
      border-radius: 100%;
      border: $base-border-width solid theme-color("primary");
      background-image: radial-gradient(theme-color("primary") 50%, transparent 50%, transparent 100%);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 0 0;
      transition: background-size .25s ease-in-out;
    }
    &:hover,
    &:focus {
      &::before {
        background-size: 66% 66%;
        // background: radial-gradient(#fcd11b 33%,transparent 33%, transparent 100%);
      }
    }
    &.active {
      &::before {
        background-size: 200% 200%;
        // background: theme-color("primary");
      }
      > ul {
        display: block;
        z-index: 10;
        > li {
          transform: translateY(0);
          opacity: 1;
        }
      }
    }
    > ul {
      position: absolute;
      display: block;
      width: 75%;
      top: 0;
      left: 100%;
      padding-left: 5rem;
      margin-left: 3rem;
      font-weight: 400;
      text-transform: none;
      z-index: 9;
      > li{
        display: flex;
        align-items: start;
        margin-bottom: 2rem;
        transform: translateY(2rem);
        opacity: 0;
        transition: all .25s ease-in-out;
        &::before {
          flex: 0 0 2rem;
          display: block;
          margin-right: 1.65rem;
          content: $scramble-dot;
          font-family: "scramble" !important;
          font-size: 2.25rem;
          font-weight: 400;
          line-height: 1;
          color: theme-color("primary");;
          speak: none;
          text-transform: none;
          margin-left: -4rem;
        }
      }
    }
  }
}
ul,ol {
  &.dot,
  &.radio
  /* &.sm-dot.visible*/ {
    opacity: 0;
    transform: translateY(2rem);
    transition: all 0.35s ease-in-out;
  }
}
.scrolled-in {

  .spoiler {
    font-size: 1em; //display: none;
    opacity: 0 !important;
    line-height: 0;
    // transition: line-height 0.25s ease-in-out .2s, opacity 0.25s ease-in-out;
    &.more {
      opacity: 1 !important;
      font-size: 1em;// display: inline-block;
      line-height: 1.45;
      // transition: line-height 0.25s ease-in-out, opacity 0.25s ease-in-out .2s;
      + .toggle {
        &::before {
          // display: inline-block;
          content: "less";
          // cursor: pointer;
        }
      }
    }
    + .toggle {
      display: flex;
      align-items: center;
      cursor: pointer;
      color: theme-color("primary");
      font-size: 2.5em;
      line-height: 0;
      // margin-top: 1rem;
      padding: 1rem 0;
      i {
        transition:  all 0.25s ease-in-out;
        margin-right: .2em;
      }
    &:hover {
        i {
          margin-right: .3em;
        }
      }
      &::before {
        display: inline-block;
        content: "more";
        cursor: pointer;
        text-transform: uppercase;
        color: black;
        font-size: .4em;
        font-weight: 600;
        margin-right: .5em;
      }
    }
  }
  div.dot {
    transform: scale(1);
  }
  ul,ol {
    &.dot {
      opacity: 1;
      transform: translateY(0);
      transition: all 0.25s ease-in-out .4s;
      li:before,
      li {
        transform: scale(1);
        opacity: 1;
        // transition: all 0.25s ease-in-out;
      }
    }
    &.radio
    /* &.sm-dot.visible */ {
      opacity: 1;
      transform: translateY(0);
      transition: all 0.25s ease-in-out .4s;

    }
  }
  ul.dot > li:hover::before {
    transform: scale(1.2);
    transition: all 0.25s ease-in-out;

  }
  span.tooltip {
    top: -6rem;
    left: 3rem;
    opacity: 1;
    transform: scale(1);
    &.hide {
      top: 0;
      left: 0;
      opacity: 0;
      transform: scale(0);
    }
  }
}
