div#loader {
  position: fixed;
  z-index: 100000;
  width: 100%;
  height: 100%;
  display: flex;
  margin: 0;
  top: 0;
  font-size: 10.25rem;
  overflow: hidden;
  scroll-behavior: smooth;
  // background-color: white;
  transition: all 1s ease-in-out 1s;
  // background-color: transparent;
  &:before {
    content: "";
    position: absolute;
    top: calc(50% - 15rem);
    left: calc(50% - 15rem);
    width: 0;
    height: 0;
    padding: 15rem;
    border-radius: 50%;
    // transform: translate(0, -50%);
    background-color: rgba(theme-color("primary"), 1);
    transform: scale(0);
    transition: transform ease-in-out 1s;
  }
  div {
    opacity: 0;
    display: block;
    position: relative;
    max-height: 13rem;
    top: calc(50% - 9rem );
    margin: 0 auto;
    transition: all .1s ease-in-out 1s;
    i {
      position: relative;
      display: block;
      white-space: nowrap;
      overflow: hidden;
      color: #fff;
      line-height: 1.3;
      transition: all ease-in-out 1s;
      // transform: translateX(-50%);
    }
    i+i {
      position: absolute;
      left: 0;
      top: 0;
      color: #000;
      max-width: 0;
    }
  }
}
body.loading {
  div#loader {
    // z-index: 100000;
    &:before {
      background-color: rgba(theme-color("primary"), 1);
      transform: scale(4);
    }
    div {
      opacity: 1;
    }
  }
}
