header {
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  background: #fff; //linear-gradient(to bottom, white, white 70%, transparent);
  // padding-bottom: 5rem;
  z-index: 1000;
  &.about {
    background: transparent;
  }
  &.scrolled-in, &.header {
    background: transparent;
    nav#main {
      .logo {
        position: absolute;
        width: 100%;
        // padding-right: 8rem;
        // margin: 0;
        top: calc(50vmin - 9.75rem);
        left: 0;
        font-size: 10.25rem;
        text-align: center;
        //  margin-bottom: 1rem;
        p.title {
          font-size: 2.7rem;
          // transition:  all 0.5s ease-in-out;
          margin-top: -2rem;
          opacity: 1;
        }
      }
      #menu {
        margin-left: 10.9rem;
        .dots {
          a {
            opacity: 0;
            font-size: 0;
            // display: none;
          }
          a:nth-child(-n+3) {
            opacity: 1;
            font-size: 2.5rem;
            // display: inline-block;
          }
        }
        &:hover>div {
          .dots a {
            display: inline-block;
            opacity: 1;
            font-size: 2.5rem;
            // transition:  all 0.25s ease-in-out;
          }
        }
      }
    }
  }

  nav#main  {
    // position: relative;
    margin-left: 0rem;
    width: 100%;
    // height: 3.5rem;
    display: flex;
    align-items: center;
    align-content: center;
    padding: 1rem 3rem 1rem 4rem;
    // background: #fff; //linear-gradient(to bottom, white, white 70%, transparent);

    .logo {
      position: fixed;
      display: block;
      flex: 0 0;
      top: 2rem;
      left: 3.8rem;
      margin-right: 3.25rem;
      width: 8rem;
      text-align: center;
      transition:  all 0.5s ease-out;
      a {
        text-decoration: none;
        text-transform: uppercase;
        color: black;
        white-space: nowrap;
      }
      p.title {
        font-size: 0;
        font-weight: 600;
        transition:  all 0.5s ease-out;
      }

    }
    #menu {
      margin-left: 10.9rem;
      display: flex;
      align-items: center;
      flex: 1 1;
      >div{
        border-radius: 2rem;
        transition:  all 0.25s ease-in-out;
        cursor: default;
        display: flex;
        align-items: center;
        padding: 0 1rem;

        .dots {
          cursor: default;
          display: flex;
          align-items: center;
              // margin-top: .5rem;
          margin-right: 1.25rem;
          font-size: 2.5rem;
          line-height: 0;
          a {
            cursor: default;
            padding-top: 0.5rem;
            // margin-right: -0.125em;
            margin-left: .25rem;
            margin-right: .25rem;
            display: inline-block;
            transition:  all 0.25s ease-in-out;

          }
          a.active {
            color: theme-color("primary");
          }
        }
        .links {
          display: flex;
          align-items: center;
          // margin-top: .5rem;
          // padding-left: 1rem;
          line-height: 1;
          a.active {
            // display: inline-block;
            font-size: 1rem;
            opacity: 1;
            transition:  all 0.25s ease-in-out;

          }
          a {
            // display: none;
            font-size: 0;
            opacity: 0;
            padding: 1rem 0 .9rem;
            margin-right: 0;//1rem;
            transition:  all 0.25s ease-in-out;
          }
        }
        a {
          // flex: 0 0;
          white-space: nowrap;
          font-weight: bold;
          text-decoration: none;
          text-transform: uppercase;
          color: black;
          margin: 0;
          padding: 0;
          transition:  all 0.25s ease-in-out;

        }
      }
      &:hover>div {
        background-color: theme-color("primary");
        border-radius: 2rem;
        color: white;
        transition:  all 0.25s ease-in-out;
        a {
          display: inline-block;
          opacity: 1;
          min-width: initial;
          // transition:  all 0.25s ease-in-out;
        }
        .dots {
          a, a.active {
            display: inline-block;
            color: white;
            padding-top: 0.5rem;
          }
        }
        .links {
          a {
            margin-right: 1.25rem;
            font-size: 1rem;
          }
          a.active, a:hover, a:focus {
            display: inline-block;
            color: white;
            font-size: 1rem;

          }
        }
      }
  }
    >a {
      font-size: 1rem;
      display: inline-block;
      position: relative;
      // margin-left: auto;
      padding: 1rem;
      font-weight: bold;
      text-decoration: none;
      text-transform: uppercase;
      color: black;
      &.dot {
        // padding-right: 2rem;
        position: relative;
        &::before {
          content: "";
          display: block;
          position: absolute;
          width: 0;
          height: 0;
          padding: 1rem;
          margin-top: 0rem;
          top: 0.6125rem;
          right: 0rem;
          z-index: -1;
          border-radius: 100%;
          background-color: theme-color("primary");
          transition: all .25s ease-in-out;
        }
      }
      &.dot:hover::before {
        transform: scale($base-hover-scale);
        // padding: 1.25rem;
        // top: 0.5rem;
        // right: -.125rem;
      }
    }
  }
}


nav#side {
  z-index: 1000;
  transition: all .25s ease-in-out;
  a.btn-outline-circle {
    transition: all .25s ease-in-out;
    i {font-size: 2rem;}
    &.hover-caption:hover {
      i {
        opacity: 0;
        font-size: 0;
      };
    }
  }

  &.header {
    left: 12rem;
    >div {
      a#up {
        opacity:0;
      }
      a#down {
        &, &::before {
          border: 2px solid transparent; //.125rem theme-color("primary") solid;
          border-radius: 100%;

        }
        margin-top: 8rem;
        background-color: transparent;
        background-size: 0 0;
        // padding: 0;
        &:hover {
          font-size: $button-font-size;
          &::before {
            background-color: transparent;
            border: none;
          }
          &::after {
            content: "";
            transform: rotate(0deg);
          }
          i {
            display: block;
            font-size: 1em;
            opacity: 1;
            // transform: translateY(1rem);
            animation: bounce 1s cubic-bezier(0.5, -0.5, 0.7, 1) infinite;
          }
        }
      }
    }
  }
  &.contact {
    >div a#down {
      background-size: 200% 200%;

      // background-color: #fcd11b;
      border-color: #fcd11b;
      i {
        font-size: 0;
        opacity: 0;
      }
      &::after,&:hover::after {
        content: "";
        transform: rotate(0deg);
        // font-size: $button-font-size;
      }
    }
  }
  position: sticky;
  left: 4rem;
  bottom: 2rem;
  width: 8rem;
  font-size: $button-font-size;
  >div {
    position: absolute;
    bottom: 0;
    width: 8rem;
    padding: 2.5rem 1rem;
    a.btn-outline-circle {
      position: relative;
      // font-size: $button-font-size;
      display: block;
      // margin: .5em 0;
      // padding: 2px;
      color: black;
      // &::before {
      //   // content: "";
      //   // padding: 50%;

      // }
      // &::after {
      //   font-size: .5em;
      //   text-transform: uppercase;
      //   transform: rotate(90deg);
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   width: 100%;
      //   line-height: 6em;
      //   text-align: center;
      //   // border: 1px solid #000;
      // }
      &:hover {
        i {opacity: 0;};
      //   &#up::after {
      //     content: "Prev";
      //   }
      //   &#down::after {
      //     content: "Next";
      //   }
      }
    }
  }
}
@keyframes bounce {
  0%   { transform: translateY(0); }
  // 10%  { transform: translateY(.1rem); }
  50%  { transform: translateY(1rem); }
  // 90%  { transform: translateY(.1rem); }
  // 95%  { transform: translateY(-.1rem); }
  100% { transform: translateY(0); }
}
