html {
  font-family: $font-family-base;
  font-weight: 400;
  font-size: 100vmax*16/1440;
  line-height: 1.45;
}

p.logo {

}
// p.title {
//   font-size: 2.5*1.2vmax;
//   // line-height: 3.5rem;
// }
p.headline {
  font-size: 2.5rem;
  font-weight: 600;
  // line-height: 3.5rem;
}

p.lead {
  font-size: 1.5em;
  // line-height: 2rem;
}

p, span {
  margin-bottom: 1rem;
  opacity: 0;
  transform: translateY(2rem);
  transition: all 0.35s ease-in-out;
}

.scrolled-in {
  p.headline {
    transition: all 0.35s ease-in-out;
  }
  p.lead {
    transition: all 0.35s ease-in-out .1s;

  }
  p, span {
    opacity: 1;
    transform: translateY(0rem);
    transition: all 0.35s ease-in-out .2s;
    }
}


form {
  input,
  small,
  label,
  .small {
    font-size: .75em;
    // line-height: 1rem;
  }
}
